}

.nav-links .link {
  cursor: pointer;
  color: black;
}

li {
  margin: 20px;
  border-bottom: 1.5px solid pink;
  padding-bottom: 10px;
}

.icon {
  display: none;
}

.links:hover {
  border-bottom: 1px solid black;
}
@media screen and (max-width: 750px) {
  .nav-links {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 1000px;
    width: 209px;
    background-color: #f5f3eb;

    transform: translateX(-500px);
    transition: 500ms;
    z-index: 2;
  }

  .nav-links li {
    position: relative;
    top: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
    background: #f5f3eb;
  }

  .nav-links li:hover {
    border-bottom: 2px solid black;
  }

  .icon {
    display: block;
    position: absolute;
    top: 19px;
    left: 5%;
    z-index: 5;
  }
}

